import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import Home from './components/Home.vue'
import About from './components/About.vue'
import Memo from './components/Memo.vue'

//const App = { template: '<div><div id="app"><router-view /></div></div>' }

/////////////////////////////////////////////////////////////
// fontawesome
import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

library.add(faUserSecret)

Vue.component('font-awesome-icon', FontAwesomeIcon)
    ///////////////////////////////////////////////////////////

Vue.config.productionTip = false


// 전역변수 여기 넣기 (production 상황에 따라 다른 주소를 넣어줘야해요)
Vue.prototype.$authUrlBase = (Vue.config.productionTip) ?
    // 'https://fapi.thesuper.kr/fapi/oauth2/authorization/' : 'https://dev.thesuper.kr:8443/fapi/oauth2/authorization/'
    'https://fapi.thesuper.kr/fapi/oauth2/authorization/' : 'https://fapi.thesuper.kr/fapi/oauth2/authorization/'

const router = new VueRouter({
    mode: 'history',
    routes: [
        { path: '/', component: Home },
        { path: '/about', component: About },
        { path: '/memo', component: Memo }
    ]
})

new Vue({
    router,
    //    render: h => h(App)
}).$mount('#app')